 @use "global.module.scss" as *;

 #container {
     background-color: #f7f7f7;
     margin: 12px 0 0 0;
     display: flex;
     flex-direction: column;
     padding: 12px;
     border-radius: 12px;
     box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
 }

 .shelf {
     background-color: white;
     display: flex;
     flex-direction: row;
     justify-content: space-evenly;
     align-items: center;
     height: 60px;
     padding: 12px;
     border-radius: 12px;
     box-sizing: border-box;
     box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
 }

 .icon {
     position: relative;
     padding: 6px;
     width: 39px;
     height: 39px;
     border-radius: 12px;
     cursor: pointer;
     transition: all 0.1s ease-in-out;

     &:hover {
         background-color: $toolHover;
         scale: 1.1;
         transition: all 0.1s ease-in-out;
     }

     &:active {
         background-color: $toolActive;
         scale: 1;
         transition: all 0.1s ease-in-out;
     }
 }

 #faFilePdf {
     color: #973126;

     &:hover {
         color: $primaryColor;
     }
 }

 #faFileExcel {
     color: #0a9544;

     &:hover {
         color: $primaryColor;
     }
 }

 #faFilePowerpoint {
     color: #e74c3c;

     &:hover {
         color: $primaryColor;
     }
 }

 .tool {
     color: $toolColor;
     position: relative;
     padding: 6px;
     width: 21px;
     height: 21px;
     border-radius: 12px;
     cursor: pointer;
     transition: all 0.1s ease-in-out;

     &:hover {
         background-color: $toolHover;
         color: $primaryColor;
         scale: 1.1;
         transition: all 0.1s ease-in-out;
     }

     &:active {
         background-color: $toolActive;
         color: #005077;
         scale: 1;
         transition: all 0.1s ease-in-out;
     }
 }

 .resetDashboard {
     color: black;
     background-color: white;
     margin: 12px 0;
     height: 60px;
     display: flex;
     flex-direction: row;
     justify-content: center;
     align-items: center;
     font-weight: bold;
     font-size: 15pt;
     border: none;
     border-radius: 12px;
     box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .18);
     cursor: pointer;
     user-select: none;
     transition: all 0.1s ease-in-out;

     &:hover {
         scale: 1.02;
         box-shadow: 0 2px 3px 1px rgba(0, 0, 0, .24);
         transition: all .1s ease-in-out;
     }

     &:active {
         scale: 1;
         box-shadow: inset 0 2px 2px 1px rgba(0, 0, 0, .18);
         transition: all 0.1s ease-in-out;
     }

     p {
         font-size: 24px;
         margin: -3px 12px 0 0;
     }
 }