@use "global.module.scss" as *;

#wrapper {
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#dashboard {
    background-color: white;
    color: black;
    margin: 0;
    padding: 0;
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform-origin: top left;
    border: 1px solid #ddd;
    border-radius: 12px;
    overflow: hidden;
    scroll-behavior: none;
    z-index: 0;
}

#mainContent {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#topContainer {
    margin: 0;
    padding: 0;
    position: relative;
    display: flex;
    flex-direction: row;
}

#tableauBlocker {
    background-color: white;
    margin: 0;
    position: absolute;
    bottom: 0px;
    height: 25px;
    width: 100%;
}