@use "global.module.scss" as *;

.wrapper {
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container {
    margin: 24px 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    width: 900px;
    border-radius: 15px;

    @media (max-width: 900px) {
        width: 425px;
    }

    @media (max-width: 425px) {
        width: 100vw;
    }
}

.header {
    margin: 24px 0 0 0;
    font-size: 18pt;
    text-align: center;
}

.link {
    color: black;
    background-color: white;
    margin: 12px;
    padding: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 15px;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
    text-decoration: none;
    overflow: hidden;
    transition: all 0.1s ease-in-out;

    &:hover {
        color: white;
        background-color: #035876;
        box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.45);
        border: none;
        scale: 1.08;
        transition: all 0.2s ease-in-out;
    }
}

.label {
    margin: 0 0 12px 0;
    text-align: center;
}

.thumbnail {
    border-radius: 9px;
    overflow: hidden;
    object-fit: contain;
    width: 360px;
    height: 203px;

    @media (max-width: 425px) {
        width: 100%;
        height: 56vw;
    }
}