@use "global.module.scss" as *;

#sidebar {
    display: flex;
    flex-direction: column;
    position: relative;
    min-width: 210px;
}

#mobileContainer {
    background-color: $primaryAccent;
    position: absolute;
    top: 105px;
    left: -240px;
    padding: 9px 21px 21px 21px;
    border-radius: 15px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    transition: all 0.1s ease-in-out;
    z-index: 2;
    opacity: 0;
}

#desktopContainer {
    background-color: white;
    padding: 0 0 0 12px;
    z-index: 1;
}


.arrow {
    background-color: $primaryAccent;
    color: white;
    position: absolute;
    top: 0;
    right: -24px;
    padding: 12px;
    font-size: 100%;
    width: 27px;
    height: 27px;
    z-index: -1;
    border-radius: 50%;
}