@use "global.module.scss" as *;

#wrapper {
    background-color: white;
    margin: 0;
    padding: 0 15px;
    max-width: 600px;
    width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 33vh;
    border-radius: 15px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    transition: all 0.2s ease-in-out;
    pointer-events: none;
    opacity: 0;
    z-index: 1;

    @media (max-width: 660px) {
        width: 80%;
        max-width: 80%;
    }
}

#header {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;

    #title {
        margin: 45px auto 0 0;
        padding: 15px 0;
        font-size: 18pt;
    }

    #close {
        color: $toolColor;
        margin: 15px 0 auto auto;
        padding: 9px;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        cursor: pointer;

        &:hover {
            background-color: $toolHover;
            color: $primaryColor;
            scale: 1.1;
            transition: all 0.2s ease-in-out;
        }

        &:active {
            background-color: $toolActive;
            color: $primaryColor;
            transition: all 0.2s ease-in-out;
        }
    }
}


#input {
    margin: 15px 0 0 0;
    padding: 15px;
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    max-height: 600px;
    height: 210px;
    border: 1px solid $toolColor;
    border-radius: 15px;
    resize: vertical;
    overflow: scroll;

    &:focus {
        outline: none;
    }
}

#submit {
    background-color: $primaryColor;
    color: white;
    margin: 15px 0 15px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12pt;
    width: 90px;
    height: 40px;
    border: none;
    border-radius: 12px;
    box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .18);
    cursor: pointer;

    &:hover {
        scale: 1.05;
        box-shadow: 0 2px 3px 1px rgba(0, 0, 0, .24);
        transition: all 0.2s ease-in-out;
    }

    &:active {
        background-color: $primaryColor;
        scale: 1;
        color: white;
        box-shadow: inset 0 2px 2px 1px rgba(0, 0, 0, .18);
        transition: all 0.2s ease-in-out;
    }
}